.dash-parent{
    display: flex;
    width: 80%;
    margin-top: 5vw;
}
.dash-items{
    height: 350px;
    width: 299px;
    border-radius: 32px;
    display: flex;
    flex-direction: column;
    background: #4568dc; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #4568dc, #b06ab3); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #4568dc, #b06ab3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    overflow: hidden;
}
.dash-items-name{
    color: white;
    font-size: 24px;
    text-align: center;
    margin-top: 20px;
    font-weight: 700;
}
.dash-items-details{
    text-align: center;
    margin-top: 10px;
    width: 260px;
    font-weight: 600;
}
.dash-btn{
    background: rgba(255, 255, 255, 0.4);
    border-radius: 33px;
    width: 110px;
    height: 35px;
    font-size: 14px;
    cursor: pointer;
    margin-top: 10px;
}
.dash-btn:hover {
    background: white;
}
.dash-items>div {
    height: 168.3px !important;
}
#transfer-item > div {
    background: #f7ebd6;
}
#gamble-item > div {
    background: #ace9f1;
}
@media screen and (max-width: 1200px) {
    .dash-parent{
        flex-direction: column;
    }
    #transfer-item{
        margin-top: 5vh;
    }
    #gamble-item{
        margin-top: 5vh;
    }
}